
:root {
  --main-bg-color: rgb(40, 44, 52);
}

body{
  background-color: var(--main-bg-color);
  overflow: hidden;
}

.panel {
  /* display: flex; */
  max-width: 600px;
}

.layout {
  position: absolute;
  margin: auto;
  max-width: 600px;
  min-width: 200px;
  top: 50%;
  left: 50%;
  margin-left: -300px;
  margin-top: -150px;
}

@media (max-width: 599px) {
  .layout{position: absolute;
    margin: auto;
    max-width: 200px;
    top: 50%;
    left: 50%;
    margin-left: -100px;
    margin-top: -300px;}
}

.ability {
  width: 80px;
  height: 100px;
  margin-left: 10px;
  margin-right: 10px;
  display: inline-block;
  border-radius: 25px;
  transition-property: background-color;
  transition-duration: .5s;
}

/* .ability:hover {
  background-color: #323741; 
} */

.ability .lab, .roll {
  text-align: center;
  color: white;
  font-size: 20pt;
  padding: 0;
  margin: 0;
}

.ability .roll {
  font-size: 40pt;
}

.ability .lab{
  padding-top: 5px;
}

#str .lab {
  color: #ff9aa2;
}

#dex .lab {
  color: #ffb7b2;
}

#con .lab {
  color: #ffdac1;
}

#int .lab {
  color: #e2f0cb;
}

#wis .lab {
  color: #b5ead7;
}

#cha .lab {
  color: #c7ceea;
}

.button-div{
  
  padding-top: 20px;
  text-align: center;
}

.gradient{
  top: -70px;
  position:relative;
  z-index:2;
  right:0; bottom:0; left:0;
  height: 200px; /* adjust it to your needs */
  background: linear-gradient(to bottom,  rgba(40,44,52,0) 0%,rgba(40,44,52,1) 30%);
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  
}

@media (max-width: 599px) {
  .gradient{
    display: none;
  }
}
/* .gradient:hover{
  top: 100px;
  transition: visibility 0s, opacity 0.5s linear;
  opacity: 0;
} */

button {
  background-color: var(--main-bg-color);
  border: solid;
  border-color: #505050;
  border-width: 1px;
  color: #f1f1f1;
  padding: 8px 30px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 10px;
  margin: 5px;
  transition-property: background-color;
  transition-duration: .5s;
  
}
button:hover, .ability:hover{
  background-color: #3d4352; /* #323741; */
}
button:focus {
  outline:0;
}
@media (max-width: 599px){
  .ability:hover{
    background-color: var(--main-bg-color);
  }
}

.active {
  background-color:  #3d4352;
}

.roll-button:hover {
  animation: rotate 0.7s ease-in-out both;
}
.roll-button:hover span {
  animation: storm 0.7s ease-in-out both;
  animation-delay: 0.06s;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
  25% {
    transform: rotate(3deg) translate3d(0, 0, 0);
  }
  50% {
    transform: rotate(-3deg) translate3d(0, 0, 0);
  }
  75% {
    transform: rotate(1deg) translate3d(0, 0, 0);
  }
  100% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
}
@keyframes storm {
  0% {
    transform: translate3d(0, 0, 0) translateZ(0);
  }
  25% {
    transform: translate3d(4px, 0, 0) translateZ(0);
  }
  50% {
    transform: translate3d(-3px, 0, 0) translateZ(0);
  }
  75% {
    transform: translate3d(2px, 0, 0) translateZ(0);
  }
  100% {
    transform: translate3d(0, 0, 0) translateZ(0);
  }
}
